import React from "react";
import { Modal, Button, notification, Form, Radio, Select, Input } from "antd";
import PropTypes from "prop-types";
import { isMobile } from "react-device-detect";

const { Option } = Select;

export function VoteModal({ form, title, isModal, setModal }) {
    const { getFieldDecorator } = form;
    const formItemLayout = {
        labelCol: { span: 7 },
        wrapperCol: { span: 17 },
    };

    const width = isMobile ? 300 : 1020;

    function updateVoterData() {
        setModal();

        notification.success({
            message: " ",
        });
    }

    return (
        <Modal
            title={title}
            visible={isModal}
            centered
            width={width}
            onCancel={setModal}
            footer={[
                <Button key="back" onClick={setModal}>
                    
                </Button>,
                <Button
                    key="submit"
                    type="primary"
                    onClick={updateVoterData}
                >
                    
                </Button>,
            ]}
        >
            <div className={"py-3 px-3"}>
                <div className={"mb-4"}>
                       ! <br/>
                      ,      .
                          ,       .
                </div>

                <Form {...formItemLayout} layout={"vertical"}>
                    <Form.Item label={""}>
                        {getFieldDecorator("gender")(
                            <Radio.Group>
                                <Radio value="man"></Radio>
                                <Radio value="woman"></Radio>
                            </Radio.Group>,
                        )}
                    </Form.Item>
                    <Form.Item label="" hasFeedback>
                        {getFieldDecorator("age")(
                            <Select placeholder=" ">
                                <Option value="18"> 18 </Option>
                                <Option value="18-25">18-25 </Option>
                                <Option value="26-35">26-35 </Option>
                                <Option value="36-44">36-44  </Option>
                                <Option value="45-54">45-54 </Option>
                                <Option value="55-64">55-64 </Option>
                                <Option value="65+">65  </Option>
                            </Select>,
                        )}
                    </Form.Item>
                    <Form.Item label="" {...formItemLayout}>
                        {getFieldDecorator("city")(
                            <Input placeholder=" " />,
                        )}
                    </Form.Item>
                    <Form.Item label="" hasFeedback>
                        {getFieldDecorator("activity")(
                            <Select placeholder="  ">
                                <Option value=""></Option>
                                <Option value="  ">  </Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>,
                        )}
                    </Form.Item>
                    <Form.Item label={""}>
                        {getFieldDecorator("kids")(
                            <Radio.Group>
                                <Radio value="0"> </Radio>
                                <Radio value="1"> </Radio>
                                <Radio value="2-5">2-5 </Radio>
                            </Radio.Group>,
                        )}
                    </Form.Item>
                </Form>
            </div>
        </Modal>
    );
}

export default Form.create({ name: "voting" })(VoteModal);

VoteModal.propTypes = {
    title: PropTypes.string,
    isModal: PropTypes.bool,
    setModal: PropTypes.func,
    form: PropTypes.object,
};

VoteModal.defaultProps = {
    title: "",
    isModal: false,
    setModal: () => {},
    form: PropTypes.object,
};
